.slider{
width: 216px;
}

input[type=range] {
  -webkit-appearance: none;
  margin: 9px 0;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  box-shadow: 0px 0px 0.1px #000000, 0px 0px 0px #0d0d0d;
  background: rgba(151, 151, 151, 0.78);
  border-radius: 0px;
  border: 0px solid #010101;
}
input[type=range]::-webkit-slider-thumb {
  box-shadow: 0px 0px 0px #000031, 0px 0px 0px #00004b;
  border: 3px solid #f47a23;
  height: 23px;
  width: 23px;
  border-radius: 50px;
  background: #222222;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -9px;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: rgba(164, 164, 164, 0.78);
}
input[type=range]::-moz-range-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  box-shadow: 0px 0px 0.1px #000000, 0px 0px 0px #0d0d0d;
  background: rgba(151, 151, 151, 0.78);
  border-radius: 0px;
  border: 0px solid #010101;
}
input[type=range]::-moz-range-thumb {
  box-shadow: 0px 0px 0px #000031, 0px 0px 0px #00004b;
  border: 3px solid #f47a23;
  height: 23px;
  width: 23px;
  border-radius: 50px;
  background: #222222;
  cursor: pointer;
}
input[type=range]::-ms-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: rgba(138, 138, 138, 0.78);
  border: 0px solid #010101;
  border-radius: 0px;
  box-shadow: 0px 0px 0.1px #000000, 0px 0px 0px #0d0d0d;
}
input[type=range]::-ms-fill-upper {
  background: rgba(151, 151, 151, 0.78);
  border: 0px solid #010101;
  border-radius: 0px;
  box-shadow: 0px 0px 0.1px #000000, 0px 0px 0px #0d0d0d;
}
input[type=range]::-ms-thumb {
  box-shadow: 0px 0px 0px #000031, 0px 0px 0px #00004b;
  border: 3px solid #f47a23;
  height: 23px;
  width: 23px;
  border-radius: 50px;
  background: #222222;
  cursor: pointer;
  height: 5px;
}
input[type=range]:focus::-ms-fill-lower {
  background: rgba(151, 151, 151, 0.78);
}
input[type=range]:focus::-ms-fill-upper {
  background: rgba(164, 164, 164, 0.78);
}


